.App {
  font-family: sans-serif;
  text-align: center;
}

img {
  max-width: 100%;
}
.position-relative {
  position: relative;
}
.btn {
  border: none;
  padding: 8px 20px;
  cursor: pointer;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  border-radius: 4px;
}
.edit-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.7);
  transition: all 0.4s;
}
.edit-btn:hover {
  background-color: rgba(255, 255, 255, 1);
}
